<template>
	<div id="signin">
		<div class="signin-form">
			<form @submit.prevent="onSubmit">
				<div class="input">
					<label for="email">Email</label>
					<input type="email" id="email" v-model="email" />
				</div>
				<div class="input">
					<label for="password">Password</label>
					<input type="password" id="password" v-model="password" />
				</div>
				<div class="submit">
					<div class="buttons">
						<button class="button is-medium is-primary is-rounded">Submit</button>
						<router-link to="/dashboard" class="button is-active">Go to My Account</router-link>
					</div>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			email: '',
			password: '',
		};
	},
	methods: {
		onSubmit() {
			const formData = {
				email: this.email,
				password: this.password,
			};
			// console.log(formData);
			this.$store.dispatch('login', { email: formData.email, password: formData.password });
			// this.$vToastify.success('easy-peasy');
		},
	},
};
</script>

<style scoped>
.signin-form {
	width: 400px;
	margin: 30px auto;
	border: 1px solid #eee;
	padding: 20px;
	box-shadow: 0 2px 3px #ccc;
}

.input {
	margin: 10px auto;
}

.input label {
	display: block;
	color: #4e4e4e;
	margin-bottom: 6px;
}

.input input {
	font: inherit;
	width: 100%;
	padding: 6px 12px;
	margin-left: 10px;
	margin-right: -12px;

	box-sizing: border-box;
	border: 1px solid #ccc;
}

.input input:focus {
	outline: none;
	border: 1px solid #1eecad;
	background-color: #eee;
}

.submit button:hover,
.submit button:active {
	background-color: #1eecad;
	color: white;
}

.submit button[disabled],
.submit button[disabled]:hover,
.submit button[disabled]:active {
	border: 1px solid #ccc;
	background-color: transparent;
	color: #ccc;
	cursor: not-allowed;
}
</style>



